"use client"

import { createContext, useContext, useState, useEffect, ReactNode } from "react"
import axios from "axios"
import { useRouter } from "next/navigation"

interface User {
  id: string
  name: string
  email: string
  role: "ADMIN" | "ATTENDANT" | "SUPPLIER" | "USER"
}

interface AuthContextType {
  user: User | null
  token: string | null
  isAuthenticated: boolean
  loading: boolean
  error: string | null
  login: (email: string, password: string) => Promise<void>
  register: (name: string, email: string, password: string) => Promise<User>
  logout: () => void
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [token, setToken] = useState<string | null>(null)
  const [isAuthenticated, setIsAuthenticated] = useState(false)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const router = useRouter()

  // üîπ Load user + token from localStorage on startup
  useEffect(() => {
    const savedToken = localStorage.getItem("auth_token")
    const savedUser = localStorage.getItem("auth_user")

    if (savedToken && savedUser) {
      try {
        const parsedUser = JSON.parse(savedUser)
        setToken(savedToken)
        setUser(parsedUser)
        setIsAuthenticated(true)
      } catch {
        localStorage.removeItem("auth_token")
        localStorage.removeItem("auth_user")
      }
    }
  }, [])

  // üîπ Save user/token to state + localStorage
  const saveAuth = (userData: User, token: string) => {
    setUser(userData)
    setToken(token)
    setIsAuthenticated(true)
    localStorage.setItem("auth_token", token)
    localStorage.setItem("auth_user", JSON.stringify(userData))
  }

  // üîπ Login user and redirect based on role
  const login = async (email: string, password: string) => {
    setLoading(true)
    setError(null)

    try {
      const response = await axios.post(
        "http://localhost:5000/api/auth/login",
        { email, password },
        { headers: { "Content-Type": "application/json" } }
      )

      const data = response.data
      const userData: User = {
        id: data.user.id,
        name: data.user.name,
        email: data.user.email,
        role: data.user.role || "user",
      }

      saveAuth(userData, data.token)

      // ‚úÖ Redirect based on role
      if (userData.role === "ADMIN") router.push("/admin/dashboard")
      else if (userData.role === "ATTENDANT") router.push("/attendant/dashboard")
      else if (userData.role === "SUPPLIER") router.push("/supplier/dashboard")
      else router.push("/")

    } catch (err: any) {
      console.error("Login failed:", err)
      setError(err.response?.data?.message || "Login failed")
      throw err
    } finally {
      setLoading(false)
    }
  }

  // üîπ Only admin can register new users
  const register = async (name: string, email: string, password: string) => {
    if (!user || user.role !== "ADMIN") {
      const message = "Only admin can register new users"
      setError(message)
      throw new Error(message)
    }

    setLoading(true)
    setError(null)

    try {
      const response = await axios.post(
        "http://localhost:5000/api/auth/register",
        { name, email, password },
        {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
        }
      )

      const data = response.data
      const newUser: User = {
        id: data.user.id,
        name: data.user.name,
        email: data.user.email,
        role: data.user.role || "user",
      }

      return newUser
    } catch (err: any) {
      console.error("Registration failed:", err)
      setError(err.response?.data?.message || "Registration failed")
      throw err
    } finally {
      setLoading(false)
    }
  }

  // üîπ Logout user
  const logout = () => {
    setUser(null)
    setToken(null)
    setIsAuthenticated(false)
    localStorage.removeItem("auth_token")
    localStorage.removeItem("auth_user")
    router.push("/login")
  }

  return (
    <AuthContext.Provider
      value={{
        user,
        token,
        isAuthenticated,
        loading,
        error,
        login,
        register,
        logout,
      }}
    >
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (!context) throw new Error("useAuth must be used within AuthProvider")
  return context
}
